import React from 'react'
import image from '../images/5.jpeg'
import '../Styles/SinglePost.css'
import {Link} from 'react-router-dom'

function SinglePost(props) {
    return (
        <div className='flex-container'>
          <div className="upper">
          <Link className="link" to={`/userdetails/${props.id}`}>
               <div className="user-link">
               <img className='profile' src={props.profile} alt="" />
                <div className="right">
                    <h3>{props.name}</h3>
                    <h4>{props.location}</h4>
                </div>
               </div>
          </Link>
            </div>
            <div className="middle">
                <img src={props.feed} className="posted-image" alt="" />
            </div>
                <p>{props.caption}</p>
            <Link to={`/comments/${props.doc}`}><div className="lower">
               {props.comments} comments
            </div></Link>
        </div>
    )
}

export default SinglePost
