import React, {useState, useEffect} from 'react'
import SinglePost from '../Components/SinglePost' 
import {projectFirestore} from '../Firebase/config'
import {Link} from 'react-router-dom'

function HomePage(props) {

    
    const [posts, setposts] = useState()
    let results = []

  
  useEffect(() => {
     projectFirestore.collection('posts').orderBy('comments').onSnapshot(snap => {
     snap.docs.forEach(doc => {
     results.push({id: doc.id, data: doc.data()})
     })
     setposts(results)
   })
  })


    return (
     <React.Fragment>
          {props.user &&     <div className="grid-container">
      { posts && posts.map(post => {
          return (
              <SinglePost key={post.id}
              doc={post.id}
              profile={post.data.profile}
              name={post.data.name}
              location={post.data.location}
              caption={post.data.caption}
              feed={post.data.post}
              comments={post.data.comments.length}
              id={post.data.id}
              />
          )
      })}
    </div>}

    {!props.user && <div className="not-user">
    <h1 className="title-insta">Welcome to <span className="title-logo">InstaGram</span> </h1>
    <p>Looks Like You Are Not Logged In. <Link to='/aboutUser' className="title-link"><span>Sign in </span></Link> to <span className='slogan'> Explore the world of InstaGram.</span></p>
      </div>}
     </React.Fragment>
    )
}

export default HomePage
